* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.square {
  background: #fff;
  border: 2px solid #999;
  float: left;
  font-size: 50px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
  font-size: 25px;;
  border: 2px dotted rgb(0, 0, 0);
  text-align: center;
  background-color: #bebbbb;
  padding: 10px;
}
.game {
  display: flex;
  flex-direction: row;
  margin: auto;
  width: auto;
  position: absolute;
  left: 40%;
}

.game-info {
  margin-top: 15%;
}

